.animatedTextContainer {
    position: relative;
    --maskX: 0;
    --maskY: 0
}

.animatedTextContent {
    color: #000000;
    font-size: 100px;
    font-weight: 600;
    line-height: 1.1;
    letter-spacing: 5px;
    margin: 0;
}

.animatedTextContentClone {
    font-size: 100px;
    font-weight: 600;
    line-height: 1.1;
    letter-spacing: 5px;
    margin: 0;
    position: absolute;
    left: 0;
    top: 0;
    color: #ffffff;

    transition: all 0.4s ease-out;
    clip-path: polygon(
      0 0 ,
      calc(var(--maskX) * 1% + (var(--maskY))*0.4% ) 0,
      calc(var(--maskX) * 1% + (var(--maskY)) * -0.4%) 100%,
      0 100%
    ) ;
}
